p {
  margin: 0 0 0 0;
}
.App {
  margin: 0 70px;
  height: 100vh;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.App h1 {
  margin-top: 0;
  font-size: medium;
}
.carousel {
  overflow-x: hidden;
}
.carousel-holder {
  width: 70%;
  display: block;
}
.carousel-holder img {
  max-width: inherit;
}
.item-description {
  margin-left: 30px;
  max-height: 750px;
  align-self: center;
  display: grid;
}
small {
  font-size: 80%;
}
.sku {
  order: 2;
  color: #999;
  letter-spacing: 2px;
  font-weight: 400;
}
ul {
  padding-left: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  list-style: none;
}
li {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 20px;
}
